<template>
<div class="checkbox">
    <span class="title">{{title}}</span>
    <label class="container">
        <input type="checkbox" :value="title" @change="emitcheckchange" v-model="editcheckval">
        <span class="checkmark" :style="{width:width+'px',height:height+'px'}"></span>
    </label>
</div>
</template>

<script>
export default {
    props: ['width', 'height', 'title','editcheck'],
    data() {
        return {
            checkvalue:this.title,
            editcheckval:this.editcheck
        }
    },
    methods:{
        emitcheckchange(){
            this.$emit('emitcheck',this.checkvalue)
        }
    }
}
</script>

