<template>
<div class="createactliffurl">
    <div class="createactliffurl-block">
        <h3>活動URL</h3>
        <div class="urlwrap">
            <input type="text" id="liffurl" v-model="liffurl">
            <button @click="colpyliffurl">
                <i class="ri-clipboard-fill"></i>
            </button>
        </div>
        <fillbtn class="toeventpagebtn" btnwidth="300" btnheight="57" btnfz="24" btnname="回到活動管理頁面" @clickhandler="toeventpage"></fillbtn>
    </div>
</div>
</template>

<script>
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {

        }
    },
    methods: {
        colpyliffurl() {
            var copyText = document.getElementById("liffurl");

            /* Select the text field */
            copyText.select();
            copyText.setSelectionRange(0, 99999); /* For mobile devices */

            /* Copy the text inside the text field */
            document.execCommand("copy");

            /* Alert the copied text */
            alert("Copied the text: " + copyText.value);
        },
        toeventpage() {
            this.$router.push({
                name: 'ActivityPage',
                params: {
                    hostid: this.hostid
                }
            })
            this.$store.commit('closeliffurlblock')
            
            this.$store.commit('resetactinfo',0)
        }
    },
    components: {
        fillbtn
    },
    computed: {
        liffurl() {
            let liff = 'https://liff.line.me/1655851081-DP32Go8k/#/eventpage/'
            return liff + this.$store.state.actinfo.id;
        },
        hostid(){
            return this.$store.state.userinfo[3]
        }

    }
}
</script>

