<template>
<div class="login">
    <Headerbar login="true"></Headerbar>
    <div class="loginarea">
        <Inputoutline headername="帳號" @inputtexthandler="catchusername" placeholder="輸入帳號"></Inputoutline>
        <Inputoutline type="password" style="marginTop:32px" @inputtexthandler="catchpassword" headername="密碼" placeholder="密碼"></Inputoutline>
        <Fillbtn @clickhandler="login" style="marginTop:40px" btnwidth="500" btnheight="40" btnfz="20" btnname="登入" :loginloading="loginani"></Fillbtn>
    </div>

</div>
</template>

<script>
import Headerbar from '../components/Header'
import Inputoutline from '../components/Inputoutline'
import Fillbtn from '../components/Fillbtn'
import axios from "axios";

export default {
    data() {
        return {
            username: '',
            password: '',
            loginani:false
        }
    },
    mounted() {

    },
    methods: {
        catchusername(val) {
            this.username = val

        },
        catchpassword(val) {
            this.password = val
        },
        login() {
            this.loginani = true
            axios({
                    method: 'get',
                    url: 'https://script.google.com/macros/s/AKfycbzdJHpinlbCgevFuJri-v_SQP5Ctr03H4nvuzUwzRzgJahnMXDuiGYxO7mpSbSXEKJH/exec',
                    params: {
                        username: this.username,
                        password: this.password
                    }
                })
                .then((response) => {
                    if (response.data.length > 0) {
                        console.log(response.data[0][2])
                        window.sessionStorage.setItem('storeuserinfo', JSON.stringify(response.data[0]))
                        this.$store.commit('setuserinfo', response.data[0])
                        this.$router.push({
                            name: 'ActivityPage',
                            params: {
                                hostid: response.data[0][3]
                            }
                        })
                        this.loginani = false
                    } else {
                        alert('帳密錯誤')
                    }

                })
        }
    },
    components: {
        Headerbar,
        Inputoutline,
        Fillbtn
    }
}
</script>

<style lang="scss" scoped>
.login {
    .loginarea {
        width: 500px;
        margin: auto;
        margin-top: 10em;
    }
}
</style>
