<template>
<div class="type">
    <div class="typegroup">
        <div class="typeblock" v-for="(item,index) of typelist" :key="index" @click="clicktype(index,item.typename)" :class="{unclicked:currentindex!=index,blockclicked:currentindex==index}">
            <i :class="item.icon"></i>
            <span class="typename">
                {{item.typename}}
            </span>

        </div>
    </div>
    <div class="btnwarp">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>

</div>
</template>

<script>
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {
            typelist: [{
                    typename: '食物',
                    icon: 'ri-restaurant-2-line'
                },
                {
                    typename: '親子',
                    icon: 'ri-parent-fill'
                },
                {
                    typename: 'DIY',
                    icon: 'ri-hand-heart-fill'
                }, {
                    typename: '戶外',
                    icon: 'ri-map-pin-range-fill'
                },
                {
                    typename: '3C',
                    icon: 'ri-smartphone-fill'
                }
            ],
            // clicked: false,
            currentindex: -1,

        }
    },
    mounted() {
        let postdata = {
            datatype: 'type',
            data: ''
        }
        if (this.type.length != 0) {
            this.typelist.forEach((item, index) => {
                if (this.type == item.typename) {
                    this.currentindex = index
                    postdata.data = item.typename
                }
                this.$store.commit('setactinfo', postdata)
            });
        }

    },
    methods: {
        upstep() {
            this.$router.push({
                name: 'ActivityPage',
                params: {
                    hostid: this.hostid
                }
            })
        },
        nextstep() {
            this.$store.commit('addcreateactindex')
        },
        clicktype(index, typename) {
            if (this.currentindex == index) {
                this.currentindex = -1
            } else {
                this.currentindex = index
                let postdata = {
                    datatype: 'type',
                    data: typename
                }
                this.$store.commit('setactinfo', postdata)
            }

        }
    },
    computed: {
        actinfo() {
            return this.$store.state.actinfo;
        },
        hostid() {
            return this.$store.state.userinfo[3]
        },
        type() {
            let arr = []
            if (this.$store.state.editeventinfo == '') {
                arr = []

            } else {
                arr = this.$store.state.editeventinfo[6]
            }
            return arr
        },editmode() {
            return this.$store.state.editmode;
        }
    },
    components: {
        fillbtn
    }
}
</script>

