<template>
<div class="host">
    <div class="hostgroup">
        <hostitem v-for="(item,index) of hostlist" :key="index" :hostname="item" @checkchange="checkedhost(item)"></hostitem>
    </div>

    <div class="btnwarp">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>

</div>
</template>

<script>
import hostitem from '../components/Hostitem'
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {
            hostlist: ['協會一', '協會二']
        }
    },
    methods: {
        nextstep() {
            this.$store.commit('addcreateactindex')
        },
        checkedhost(hostname) {
            let postdata = {
                datatype: 'host',
                data: hostname
            }
            this.$store.commit('setactinfo', postdata)

        }
    },
    components: {

        hostitem,
        fillbtn
    }
}
</script>

