<template>
  <div class="basicinfo">
    <div class="basicinfo-editaera">
      <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[1]"
          :placeholder="picurl"
          @inputtexthandler="catchinputcontent"
          headername="活動封面照片"
        ></inputoutline>
        <form id="form">
          <input name="file" id="uploadfile" type="file" />
          <input name="filename" id="filename" type="text" placeholder="檔案名稱" />
          <input id="submit" type="submit" />
        </form>
        <!-- <div class="uploadpic-area">
                <div class="uploadbtn">
                    <span>上傳照片</span>
                </div>
                <div class="defaultpic">
                    <span>系統預設圖片</span>
                </div>
        </div>-->
      </div>
      <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[0]"
          placeholder="輸入活動名稱"
          @inputtexthandler="catchinputcontent"
          headername="活動名稱"
        ></inputoutline>
      </div>
      <div class="basicinfo-date">
        <div class="inputname">
          <span>選擇活動日期</span>
        </div>
        <Datepicker
          :value="editeventinfo[2]"
          @input="postdate"
          v-model="selectdate"
          input-class="pickerstyle"
          class="datepicker"
          format="yyyy-MM-dd"
        ></Datepicker>
        <!-- <inputoutline :alreadydata="editeventinfo[2]" placeholder="YYYY/MM/DD" @inputtexthandler="catchinputcontent" headername="活動日期"></inputoutline> -->
      </div>
        <div class="basicinfo-date">
        <div class="inputname">
          <span>選擇報名截止日</span>
        </div>
        <Datepicker
          :value="editeventinfo[8]"
          @input="postdeadlinedate"
          v-model="deadlinedate"
          input-class="pickerstyle"
          class="datepicker"
          format="yyyy-MM-dd"
        ></Datepicker>
        
        <!-- <inputoutline :alreadydata="editeventinfo[2]" placeholder="YYYY/MM/DD" @inputtexthandler="catchinputcontent" headername="活動日期"></inputoutline> -->
      </div>
      <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[3]"
          placeholder="上午 hh:mm - 下午 hh:mm"
          @inputtexthandler="catchinputcontent"
          headername="活動時間"
        ></inputoutline>
      </div>
      <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[4]"
          placeholder="台北市中正區..."
          @inputtexthandler="catchinputcontent"
          headername="活動地點"
        ></inputoutline>
      </div>
      <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[7]"
          @inputtexthandler="catchinputcontent"
          headername="活動上限人數"
        ></inputoutline>
      </div>
     
      <!-- <div class="basicinfo-actname">
        <inputoutline
          :alreadydata="editeventinfo[8]"
          @inputtexthandler="catchinputcontent"
          headername="備取人數"
        ></inputoutline>
      </div> -->
    </div>

    <div class="basicinfo-btnwrap">
      <fillbtn
        btnwidth="193"
        btnheight="57"
        btnfz="24"
        btnname="取消"
        @clickhandler="upstep"
        class="cancelbtn"
      ></fillbtn>
      <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>
  </div>
</template>

<script>
import inputoutline from "../components/Inputoutline";
import fillbtn from "../components/Fillbtn";
import axios from "axios";
import Datepicker from "vuejs-datepicker";
export default {
  data() {
    return {
      checklist: [],
      selectdate: "",
      deadlinedate:"",
      picurl:'照片連結'
    };
  },
  mounted() {
    this.seteditinputcontent();
    if (this.editmode == false) {
      for (let i = 1; i < 6; i++) {
        this.checklist[i] = true;
      }
    } else {
      this.deadlinedate = this.editeventinfo[8]
      this.selectdate = this.editeventinfo[2]
      for (let i = 1; i < 6; i++) {
        this.checklist[i] = false;
      }
    }
    const form = document.getElementById("form");
    form.addEventListener("submit", e => {
      e.preventDefault();
      const file = form.file.files[0];
      const fr = new FileReader();
      fr.readAsArrayBuffer(file);
      fr.onload = f => {
        const url =
          "https://script.google.com/macros/s/AKfycbwzwel2GW11DLdfKLPX_8FcrpXodRPNo2e6NPIf1izPU8xJjUstYSSg4H_xbKGAg54/exec"; // <--- Please set the URL of Web Apps.
        const qs = new URLSearchParams({
          filename: form.filename.value || file.name,
          mimeType: file.type
        });
        fetch(`${url}?${qs}`, {
          method: "POST",
          body: JSON.stringify([...new Int8Array(f.target.result)]),
          mode: "no-cors"
        })
          .then(response => {
            console.log("success:", response);
            this.test(form.filename.value);
          })
          .catch(err => {
            console.log("Error:" + err);
          });
      };
    });
  },
  methods: {
    postdate() {
      let arr = this.selectdate.toString().split(" ");
      let postdate = arr[3] + "/" + this.checkmonth(arr[1]) + "/" + arr[2];
      let postdata = {
        datatype: "date",
        data: postdate
      };
      this.$store.commit("setactinfo", postdata);
      this.checklist[2] = false;
    },
    postdeadlinedate() {
      let arr = this.deadlinedate.toString().split(" ");
      let postdate = arr[3] + "/" + this.checkmonth(arr[1]) + "/" + arr[2];
      let postdata = {
        datatype: "deadlinedate",
        data: postdate
      };
      this.$store.commit("setactinfo", postdata);
      this.checklist[8] = false;
    },
    test(filename) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbyyAihtBGFo_Y_uEFPlQ9z21sTH6riNcVWSDEHXgix2sH0jB5GL5q1cBRbTFKYM2CHl8Q/exec",
          {
            params: {
              filename: filename
            }
          }
        )
        .then(res => {
          console.log(res.data);
          this.picurl = res.data
          alert('上傳成功')
          let postdata1 = {
            datatype: "actpic",
            data: res.data
          };
          this.$store.commit("setactinfo", postdata1);
        })
        .catch(error => {
          console.error(error);
        });
    },
    changemonthformat() {},
    checkmonth(mon) {
      let month = "";
      switch (mon) {
        case "Jan":
          month = "01";
          break;
        case "Feb":
          month = "02";
          break;
        case "Mar":
          month = "03";
          break;
        case "Apr":
          month = "04";
          break;
        case "May":
          month = "05";
          break;
        case "Jun":
          month = "06";
          break;
        case "Jul":
          month = "07";
          break;
        case "Aug":
          month = "08";
          break;
        case "Sep":
          month = "09";
          break;
        case "Oct":
          month = "10";
          break;
        case "Nov":
          month = "11";
          break;
        case "Dec":
          month = "12";
          break;
      }

      return month;
    },
    upstep() {
      this.$store.commit("minuscreateactindex");
    },
    nextstep() {
      if (this.checklist.includes(true)) {
        alert("請填寫完畢");
      } else {
        this.$store.commit("addcreateactindex");
      }
    },
    seteditinputcontent() {
      let postdata2 = {
        datatype: "actname",
        data: this.editeventinfo[0]
      };
      let postdata3 = {
        datatype: "date",
        data: this.editeventinfo[2]
      };
      let postdata4 = {
        datatype: "time",
        data: this.editeventinfo[3]
      };
      let postdata5 = {
        datatype: "loc",
        data: this.editeventinfo[4]
      };
      let postdata6 = {
        datatype: "quota",
        data: this.editeventinfo[7]
      };
      let postdata7 = {
        datatype: "available",
        data: this.editeventinfo[8]
      };

     
      this.$store.commit("setactinfo", postdata2);
      this.$store.commit("setactinfo", postdata3);
      this.$store.commit("setactinfo", postdata4);
      this.$store.commit("setactinfo", postdata5);
      this.$store.commit("setactinfo", postdata6);
      this.$store.commit("setactinfo", postdata7);
    },
    catchinputcontent(inputcontent, inputheader, isempty) {
      let postdata = {
        datatype: "type",
        data: inputcontent
      };

      switch (inputheader) {
        case "活動封面照片":
          postdata.datatype = "actpic";
          this.$store.commit("setactinfo", postdata);
          this.checklist[0] = isempty;
          break;
        case "活動名稱":
          postdata.datatype = "actname";

          this.$store.commit("setactinfo", postdata);
          this.checklist[1] = isempty;
          break;
        case "活動時間":
          postdata.datatype = "time";

          this.$store.commit("setactinfo", postdata);
          this.checklist[3] = isempty;
          break;
        case "活動地點":
          postdata.datatype = "loc";

          this.$store.commit("setactinfo", postdata);
          this.checklist[4] = isempty;
          break;
        case "活動上限人數":
          postdata.datatype = "quota";

          this.$store.commit("setactinfo", postdata);
          this.checklist[5] = isempty;
          break;
        // case "備取人數":
        //   postdata.datatype = "available";

        //   this.$store.commit("setactinfo", postdata);
        //   this.checklist[6] = isempty;
        //   break;
      }
      console.log(inputcontent, inputheader);
    }
  },
  computed: {
    editeventinfo() {
      let arr = [];
      if (this.$store.state.editeventinfo == "") {
        arr = [];
      } else {
        arr = this.$store.state.editeventinfo;
      }
      return arr;
    },
    editmode() {
      return this.$store.state.editmode;
    }
  },
  components: {
    inputoutline,
    fillbtn,
    Datepicker
  }
};
</script>

