import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentlistinfo: [],
    currentactpageindex: 0,
    actinfo: {
      actname: "",
      actpic: "",
      date: "",
      time: "",
      loc: "",
      isprivate: "",
      type: "",
      quota: "",
      deadlinedate: "",
      tag: "",
      signupinfo: "",
      describe: "",
      fee: "",
      price: "",
      chargemethods: "",
      accountinfo: "",
      hostid: "",
      id: "",
    },
    editeventinfo: [],
    userinfo: [],
    liffurlshow: false,
    editmode: false,
    navnumber:0
  },
  mutations: {
    addcreateactindex(state) {
      state.currentactpageindex++;
    },
    setnavnumber(state,payload){
      state.navnumber = payload
    },
    minuscreateactindex(state) {
      if (state.currentactpageindex == 0) {
        state.currentactpageindex = 0;
      }
      state.currentactpageindex--;
    },
    showliffurlblock(state) {
      state.liffurlshow = true;
    },
    closeliffurlblock(state) {
      state.liffurlshow = false;
    },
    setactinfo(state, payload) {
      switch (payload.datatype) {
        case "isprivate":
          state.actinfo.isprivate = payload.data;
          break;
        case "actpic":
          state.actinfo.actpic = payload.data;
          break;
        case "type":
          state.actinfo.type = payload.data;
          break;
        case "actname":
          state.actinfo.actname = payload.data;
          break;
        case "date":
          state.actinfo.date = payload.data;
          break;
        case "time":
          state.actinfo.time = payload.data;
          break;
        case "loc":
          state.actinfo.loc = payload.data;
          break;
        case "quota":
          state.actinfo.quota = payload.data;
          break;
        case "deadlinedate":
          state.actinfo.deadlinedate = payload.data;
          break;
        case "tag":
          state.actinfo.tag = payload.data;
          break;
        case "signupinfo":
          state.actinfo.signupinfo = payload.data;
          break;
        case "describe":
          state.actinfo.describe = payload.data;
          break;
        case "fee":
          state.actinfo.fee = payload.data;
          break;
        case "price":
          state.actinfo.price = payload.data;
          break;
        case "chargemethods":
          state.actinfo.chargemethods = payload.data;
          break;
        case "accountinfo":
          state.actinfo.accountinfo = payload.data;
          break;
      }
    },
    _uuid(state) {
      var d = Date.now();
      if (
        typeof performance !== "undefined" &&
        typeof performance.now === "function"
      ) {
        d += performance.now(); //use high-precision timer if available
      }
      state.actinfo.id = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
          var r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
    },
    setuserinfo(state, payload) {
      state.userinfo = payload;
      state.actinfo.hostid = payload[3];
    },
    resetactinfo(state, payload) {
      state.currentactpageindex = payload;
    },
    setediteventinfo(state, payload) {
      state.editeventinfo = payload;
    },
    seteditmode(state, payload) {
      state.editmode = payload;
    },
  },
  actions: {
    postactinfo(context) {
      context.commit("_uuid");
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbxZQATEbO4erS2vyY4Udc_BriYKBXGlLuxYxC96LgIijG7_etaz_UeTqRP2SNl4qUEVwg/exec",
          {
            params: {
              actname: context.state.actinfo.actname,
              actpic: context.state.actinfo.actpic,
              actdate: context.state.actinfo.date,
              acttime: context.state.actinfo.time,
              actloc: context.state.actinfo.loc,
              isprivate: context.state.actinfo.isprivate,
              type: context.state.actinfo.type,
              actin: context.state.actinfo.quota,
              deadlinedate: context.state.actinfo.deadlinedate,
              tag: context.state.actinfo.tag,
              signupinfo: context.state.actinfo.signupinfo,
              describe: context.state.actinfo.describe,
              fee: context.state.actinfo.fee,
              price: context.state.actinfo.price,
              chargemethods: context.state.actinfo.chargemethods,
              accountinfo: context.state.actinfo.accountinfo,
              hostid: context.state.actinfo.hostid,
              id: context.state.actinfo.id,
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
    editactinfo(context) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbzPt2VBmYaaYEIYrBIP5yLM67k-jfLQCIbhVvvQk9qrGXF9LrmvO6ngF8eJ5eD_4hSa/exec",
          {
            params: {
              actname: context.state.actinfo.actname,
              actpic: context.state.actinfo.actpic,
              actdate: context.state.actinfo.date,
              acttime: context.state.actinfo.time,
              actloc: context.state.actinfo.loc,
              isprivate: context.state.actinfo.isprivate,
              type: context.state.actinfo.type,
              actin: context.state.actinfo.quota,
              deadlinedate: context.state.actinfo.deadlinedate,
              tag: context.state.actinfo.tag,
              signupinfo: context.state.actinfo.signupinfo,
              describe: context.state.actinfo.describe,
              fee: context.state.actinfo.fee,
              price: context.state.actinfo.price,
              chargemethods: context.state.actinfo.chargemethods,
              accountinfo: context.state.actinfo.accountinfo,
              id: context.state.editeventinfo[17],
             
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  modules: {},
});
