<template>
<div class="dropdown" :style="{border:bordercol}" @click.self="openmenu">
    {{cattype}}
    <i class="ri-arrow-drop-down-line"></i>
    <ul v-show="isdrop">
        <li class="item" v-for="(item,index) of catlist" :key="index" :class="{clicked: index == currentindex}" @click="closemenu(item,index)">{{item}}</li>
    </ul>
</div>
</template>

<script>
export default {
    props: ['catlist'],
    data() {
        return {
            activitycat: ['選擇活動類型', '親子', '飲食', 'DIY'],
            isdrop: false,
            isfocus: false,
            bordercol: 'solid 1px #818181',
            cattype: '選擇活動類型（全部）',
            currentindex: 0,
        }
    },
    methods: {
        openmenu() {
            if (this.isdrop == true) {
                this.isdrop = false
                this.bordercol = 'solid 1px #818181'
            } else {
                this.isdrop = true;
                this.bordercol = 'solid 1px #87ddb1'
            }
            

            // this.isfocus = true
        },
        closemenu(item, index) {
            this.isdrop = false
            this.cattype = item
            this.bordercol = 'solid 1px #818181'
            this.currentindex = index
            this.$emit('itemhandler', item)
        }
    }
}
</script>

