<template>
<div class="setting">
    <div class="nav">
        <sidemenu></sidemenu>
    </div>
    <div class="content">
        <Headerbar login="false"></Headerbar>
        <h4 class="pagetitle">
            設定
        </h4>
        <div class="hostprofile">
            <div class="hostprofile-topbar">
                <h3>基本資料</h3>
                <Roundbtn v-show="editmode==false" @emitclick="catchclick" width="90" name="編輯資料" height="25"></Roundbtn>
                <Roundbtn v-show="editmode==true" style="marginRight:15px" @emitclick="savedata" width="90" name="儲存資料" height="25"></Roundbtn>
                <Roundbtn v-show="editmode==true" @emitclick="cancel" width="90" name="取消變更" height="25"></Roundbtn>
            </div>

            <div class="name">
                <span class="title">協會名稱</span>
                <span class="name-content">{{userinfolist[2]}}</span>

            </div>
            <div class="name">
                <span class="title">使用者帳號</span>
                <div v-show="editmode==false" class="name-content">{{userinfolist[0]}}</div>
                <inputtext @inputtexthandler="editusernamae" :placeholder="username" width="500" v-show="editmode"></inputtext>

            </div>
            <div class="name">
                <span class="title">使用者密碼</span>
                <div v-show="editmode==false" class="name-content">{{userinfolist[1]}}</div>
                <inputtext @inputtexthandler="editpassword" :placeholder="password" width="500" v-show="editmode"></inputtext>

            </div>
            <div class="name">
                <div class="title">LIFF首頁URL（點擊連結複製）</div>
                <div class="wrap">
                    <input ref="mainliffurl" @click="copyliffurl" :value="'https://liff.line.me/1655851081-DP32Go8k/#/'+userinfolist[3]" class="content">
                </div>

            </div>
            <div class="name">
                <span class="title">LIFF我的活動URL（點擊連結複製）</span>
                <div class="wrap">
                    <input ref="eventliffurl" @click="copyeventliffurl" :value="'https://liff.line.me/1655851081-DP32Go8k/#/profilepage/alreadysignup/'+userinfolist[3]" class="content">
                </div>

            </div>
            <div class="name">
                <span class="title">LIFF個人資料URL（點擊連結複製）</span>
                <div class="wrap">
                    <input ref="profileliffurl" @click="copyprofileliffurl" :value="'https://liff.line.me/1655851081-DP32Go8k/#/profilepage/userinfo/'+userinfolist[3]" class="content">
                </div>

            </div>

        </div>
    </div>
</div>
</template>

<script>
import sidemenu from '../components/Navmenu'
import Headerbar from '../components/Header'
import Roundbtn from '../components/Fillroundbtn'
import inputtext from '../components/Inputoutline'
import axios from "axios";
export default {
    data() {
        return {
            editmode: false,
            newusername: '',
            newpassword: '',
            userinfolist: []
        }
    },
    created() {

        if (window.sessionStorage.getItem('storeuserinfo')) {
            var storeuserinfo = window.sessionStorage.getItem('storeuserinfo');
            this.$store.commit('setuserinfo', JSON.parse(storeuserinfo))
            this.userinfolist = JSON.parse(storeuserinfo);
        }

        // if (sessionStorage.getItem('store')) {
        //     var storeeventinfo = window.sessionStorage.getItem('store');
        //     this.$store.state.userinfo = storeeventinfo
        //     // this.$store.replaceState(
        //     //     Object.assign({},
        //     //         this.$store.state.userinfo,
        //     //         JSON.parse(sessionStorage.getItem('store'))
        //     //     )
        //     // )
        // }
        // // 在页面刷新时将Vuex里的信息保存到sessionStorage里
        // window.addEventListener('beforeunload', () => {
        //     sessionStorage.setItem('store', JSON.stringify(this.$store.state.userinfo))
        // })

    },
    computed: {
        hostid() {
            return this.$store.state.userinfo[3]
        },
        hostname() {
            return this.$store.state.userinfo[2]
        },
        username() {
            return this.$store.state.userinfo[0]
        },
        password() {
            return this.$store.state.userinfo[1]
        }
    },
    methods: {
        catchclick() {
            if (this.editmode == true) {
                this.editmode = false
            } else {
                this.editmode = true
            }

        },

        copyliffurl() {
            var liffurl = this.$refs.mainliffurl
            liffurl.select();
            liffurl.setSelectionRange(0, 99999); /* For mobile devices */

            /* Copy the text inside the text field */
            document.execCommand("copy");

            /* Alert the copied text */
            alert("已複製連結");
        },
        copyeventliffurl() {
            var eventliffurl = this.$refs.eventliffurl
            eventliffurl.select();
            eventliffurl.setSelectionRange(0, 99999); /* For mobile devices */

            /* Copy the text inside the text field */
            document.execCommand("copy");

            /* Alert the copied text */
            alert("已複製連結");

        },
        copyprofileliffurl() {
            var profileliffurl = this.$refs.profileliffurl
            profileliffurl.select();
            profileliffurl.setSelectionRange(0, 99999); /* For mobile devices */

            /* Copy the text inside the text field */
            document.execCommand("copy");

            /* Alert the copied text */
            alert("已複製連結");

        },
        cancel() {
            this.editmode = false
        },
        savedata() {
            axios({
                    method: 'get',
                    url: 'https://script.google.com/macros/s/AKfycbwI2eU_-DyBaRZsQJ9VBeApyS1tEsTtXA5huVMziWsNHqZglaHUCrCyoJFsuOvgwgnZ/exec',
                    params: {
                        username: this.username,
                        password: this.password,
                        newusername: this.newusername,
                        newpassword: this.newpassword
                    }
                })
                .then((response) => {
                    console.log(response.data)
                    this.$router.push({
                        name: 'Login',
                    })
                })
        },
        editusernamae(val) {
            console.log(val)
            this.newusername = val
        },
        editpassword(val) {
            console.log(val)
            this.newpassword = val

        }
    },
    components: {
        sidemenu,
        Headerbar,
        Roundbtn,
        inputtext
    }
}
</script>

