<template>
<div class="signupinfo">
    <div class="singupinfo-editaera">
        <div class="signupinfo-needinfo">
            <h3 class="needingo-title">
                您需要的報名資訊
            </h3>
            <draggable class="list-group" group="people" :list="list1">
                <div class="dragitem" v-for="element in list1" :key="element.name">
                    {{element.name}}
                </div>
            </draggable>

        </div>
        <div class="signupinfo-customneedinfo">

            <div class="needinfo-editerea">
                <div class="editerea-tab">
                    <div class="tab-needinfo" @click="tabclicked(0)" :class="{tabclicked:currentindex==0}">
                        <span>拖拉需要的資訊</span>
                    </div>
                    <div class="tab-custominfo" @click="tabclicked(1)" :class="{tabclicked:currentindex==1}">
                        <span>自訂欄位</span>
                    </div>

                </div>
                <draggable class="list-group" :list="list2" group="people" v-show="tabstatus=='need'">
                    <div class="dragitem" v-for="element in list2" :key="element.name">
                        {{ element.name }}
                    </div>
                </draggable>
                <div class="editerea-custominfo" v-show="tabstatus=='edit'">
                    <InputNoOutline @inputtexthandler="catchcotent" listheader="輸入自訂的報名資訊"></InputNoOutline>
                    <draggable class="list-group" group="people" :list="list3" v-show="tabstatus=='edit'">
                        <div class="dragitem" v-for="element in list3" :key="element.name">
                            {{ element.name }}
                        </div>
                    </draggable>
                </div>
            </div>

        </div>
    </div>

    <div class="signupinfo-btnwrap">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>

</div>
</template>

<script>
import draggable from 'vuedraggable'
import InputNoOutline from '../components/Inputnooutline'
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {
            list1: [{
                    name: "姓名",

                },
                {
                    name: "電話",

                },
                {
                    name: "生日",

                },
                {
                    name: "地址",

                }, {

                    name: "性別",

                }
            ],
            list2: [{
                    name: "信箱",

                },
                {
                    name: "用餐需求",

                }
            ],
            list3: [],
            currentindex: 0,
            tabstatus: 'need',

        }
    },
    mounted() {
        this.list1 = this.editsignupinfo;
    },
    methods: {
        clone: function (el) {
            return {
                name: el.name + " cloned"
            };
        },
        tabclicked(index) {
            this.currentindex = index;
            if (index == 0) {
                this.tabstatus = "need"
            } else {
                this.tabstatus = "edit"
            }

        },
        catchcotent(val) {
            let obj = {
                name: val
            }
            this.list3.push(obj)
        },
        upstep() {
            this.$store.commit('minuscreateactindex')

        },
        nextstep() {
            let postsignupinfo = ''
            this.list1.forEach(item => {
                postsignupinfo = postsignupinfo + item.name + '、'
            })
            postsignupinfo = postsignupinfo.substring(0, postsignupinfo.length - 1)

            let postdata = {
                datatype: 'signupinfo',
                data: postsignupinfo
            }

            this.$store.commit('setactinfo', postdata)
            this.$store.commit('addcreateactindex')
        },
    },
    computed: {
        dragOptions() {
            return {
                animation: 200,
                group: "description",
                disabled: false,
                ghostClass: "ghost"
            };
        },
        editsignupinfo() {
            let arr = [{
                    name: "姓名",

                },
                {
                    name: "電話",

                },
                {
                    name: "生日",

                },
                {
                    name: "地址",

                }, {

                    name: "性別",

                }
            ]
            if (this.$store.state.editeventinfo.length == 0) {
                console.log('arr')
            } else {
                let newarr = this.$store.state.editeventinfo[10].split('、')
                arr = []
                for (let i = 0; i < newarr.length; i++) {
                    let obj = {
                        name: newarr[i]
                    }
                    arr.push(obj)
                }
            }
            return arr
        }
    },
    components: {
        draggable,
        InputNoOutline,
        fillbtn
    }
}
</script>

