<template>
<div class="activitylist closed">
    <div class="icon ">
        <i :class="icon"></i>
    </div>
    <div class="actinfo">
        <div class="actname">
            <span class="name">{{actname}}</span>
            <span class="position">{{actpos}}</span>
        </div>
        <div class="acttime">
            <div class="leftdeco"></div>
            <span>{{actdate}} {{acttiime}}</span>
        </div>
    </div>
    <div class="actadvinfo">
        <div class="acttag">
            <div v-for="(tag,index) in sliceacttag" :key="index">
                <activitytag :tagname="tag"></activitytag>
            </div>

        </div>
        <div class="actpeople">
            <span>活動人數：{{actin}}人</span>
        </div>
    </div>
    <div class="notify">
        <i class="ri-notification-4-line"></i>
        <span>活動提醒</span>
    </div>
    <div class="edit" @click.stop="editevent">
        <i class="ri-file-edit-line"></i>
        <span>編輯活動</span>

    </div>
    <div @click.stop="moreoptionopen" class="moreoption">
        <i class="ri-list-unordered"></i>
        <span>進階選項</span>
        <div class="optionmenu" v-show="moreoptionisclick">
            <div class="item" @click="copyeventurl">複製活動連結</div>
            <div class="item" @click="deleact">刪除活動</div>
        </div>
    </div>
    <input type="text" ref="liffurl" v-model="liffurl">
</div>
</template>

<script>
import activitytag from '../components/Activitytag'
export default {
    props: ['actname', 'actpos', 'acttiime', 'actdate', 'acttag', 'actin', 'type', 'icon', 'id'],
    data() {
        return {
            moreoptionisclick: false,
            acttaglist: this.acttag,
            typelist: [{
                    typename: '食物',
                    icon: 'ri-restaurant-2-line'
                },
                {
                    typename: '親子',
                    icon: 'ri-parent-fill'
                },
                {
                    typename: '手做DIY',
                    icon: 'ri-hand-heart-fill'
                }, {
                    typename: '戶外旅遊',
                    icon: 'ri-map-pin-range-fill'
                }
            ],
            liffurl: 'https://liff.line.me/1655851081-DP32Go8k/#/eventpage/' + this.id
        }
    },
    methods: {
        moreoptionopen() {
            if (this.moreoptionisclick == true) {
                this.moreoptionisclick = false
            } else {
                this.moreoptionisclick = true
            }

        },
        deleact() {
            this.$emit('emitdeleact')
        },
        copyeventurl() {

            var copyText = this.$refs.liffurl

            /* Select the text field */
            copyText.select();
            copyText.setSelectionRange(0, 99999); /* For mobile devices */

            /* Copy the text inside the text field */
            document.execCommand("copy");

            /* Alert the copied text */
            alert("已複製連結");

        },
        editevent() {
            this.$emit('editeventhandler')
        }
    },
    computed: {
        sliceacttag() {
            return this.acttaglist.split('、')
        },
        eventclosed() {
            var today = new Date();
            let nowdays = new Date((today.getFullYear()) + '/' + (today.getMonth() + 1) + '/' + today.getDate());
            let eventdays = new Date(this.actdate)
            let isclosed = false
            if (nowdays > eventdays) {
                isclosed = false
            } else if (nowdays < eventdays) {
                isclosed = true
            }
            return isclosed
        }
    },
    components: {
        activitytag
    }
}
</script>

