import Vue from 'vue'
import VueRouter from 'vue-router'
import ActivityPage from '../views/Activity.vue'
import CreateAct from '../views/CreateAct.vue'
import Login from '../views/Login.vue'
import Setting from '../views/Setting.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/login',
  },{
    path:'/activityPage/:hostid',
    name: 'ActivityPage',
    component:ActivityPage
  },
  {
    path: '/login',
    name:'Login',
    component:Login
  },
  {
    path:'/setting',
    name:'Setting',
    component:Setting
  },
  {
    path: '/activitymembermanage/:eventid',
    name: 'ActivityMemberManage',
    component: () => import(/* webpackChunkName: "about" */ '../views/ActivityMemberManage.vue')
  },{
    path:'/createact',
    name:'CreateAct',
    component:CreateAct

  }
]

const router = new VueRouter({
  routes
})

export default router
