<script>
// @ is an alias to /src
import HeaderBar from '../components/Header'
import SearchBar from '../components/Searchbar'
// import Tabitem from '../components/Tabitem'
import Dropdown from '../components/Dropdown'
// import Timefilter from '../components/Timefilter'
import Sidemenu from '../components/Navmenu'
import Activitylist from '../components/Activitylist'
import Deleactpopup from '../components/Deleactpopup'
import axios from 'axios'

export default {
    data() {
        return {
            catlist: ['全部', '親子', 'DIY', '戶外', '食物'],
            itemlist: [{
                name: '所有場館',
                isclick: true
            }],
            typelist: [{
                    typename: '食物',
                    icon: 'ri-restaurant-2-line'
                },
                {
                    typename: '親子',
                    icon: 'ri-parent-fill'
                },
                {
                    typename: '手做DIY',
                    icon: 'ri-hand-heart-fill'
                }, {
                    typename: '戶外旅遊',
                    icon: 'ri-map-pin-range-fill'
                }
            ],
            sclist: [],
            fulllist: [],
            isall: true,
            isnotall: false,
            currentcata: '全部',
            currentstadium: '所有場館',
            tabcurrentindex: 0,
            keyword: '',
            isdele: false,
            deleactindex: 0,

            pagenumber: 0,
            pagesize: 4,
            loadingcircle: true,
            timestatus: ''

        }
    },
    mounted() {
        document.body.scrollTop = document.documentElement.scrollTop = 0;
        document.body.style.overflow = 'hidden'
        if (window.sessionStorage.getItem('storeuserinfo')) {
            var storeuserinfo = window.sessionStorage.getItem('storeuserinfo');
            this.$store.commit('setuserinfo', JSON.parse(storeuserinfo))
        }
        // axios({
        //         method: 'get',
        //         url: url,
        //     })
        //     .then((response) => {
        //         this.sclist = response.data.res;
        //         this.sclist.splice(0, 1)

        //         for (let i = 0; i < this.sclist.length; i++) {
        //             for (let j = 0; j < this.sclist.length; j++) {
        //                 if (this.sclist[i][0] == this.sclist[j][0] && i != j) {
        //                     this.sclist.splice(j, 1);
        //                     j = 0

        //                 }
        //             }
        //         }
        //         console.log(this.sclist)
        //     })

        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbzE-4_VcXcDJKk62FGK_2GaoncOW9DANFo-bdbUSIg9GDoO-hjy3hJTvGjF8t7d-8f-/exec',
                params: {
                    mode: 'hostid',
                    id: this.$route.params.hostid
                }
            })
            .then((response) => {
                console.log(response.data)
                this.sclist = response.data
                this.loadingcircle = false
                document.body.style.overflow = 'visible'
            })
    },
    computed: {
        filterlist: {
            get() {
                return this.sclist.filter(item => {
                    if (this.currentcata == "全部" && this.currentstadium == "所有場館" && this.keyword == "" || this.timestatus == "all") {
                        return this.sclist
                    } else {
                        if (this.currentcata == "全部" && this.currentstadium == "所有場館" && item[0].search(this.keyword) != -1) {
                            return item
                        } else if (this.currentcata == "全部") {
                            if (item[4] == this.currentstadium && item[0].search(this.keyword) != -1) {
                                //  console.log('測試',item[0])
                                return item
                            }
                        } else if (this.currentstadium == "所有場館") {
                            if (item[6] == this.currentcata && item[0].search(this.keyword) != -1) {
                                return item
                            }
                        } else if (item[4] == this.currentstadium && item[6] == this.currentcata && item[0].search(this.keyword) != -1) {
                            return item
                        }

                    }

                })
            },
            set(val) {
                this.sclist = val;

            }

        },
        pagecount() {
            let l = this.filterlist.length;
            let s = this.pagesize;
            return Math.floor(l / s)
        },
        paginatedata() {
            const start = this.pagenumber * this.pagesize
            const end = start + this.pagesize;

            return this.filterlist.slice(start, end)
        }
    },
    methods: {
        editevent(eventid) {
            this.$store.commit('seteditmode', true)
            this.sclist.forEach((item, index) => {
                if (item[17] == eventid) {
                    this.$store.commit('setediteventinfo', this.sclist[index])
                }
            });
            this.$router.push({
                name: 'CreateAct'
            })

        },
        checkeventicon(type) {
            switch (type) {
                case '食物':
                    return 'ri-restaurant-2-line';

                case '親子':
                    return 'ri-parent-fill';

                case 'DIY':
                    return 'ri-hand-heart-fill';

                case '戶外':
                    return 'ri-map-pin-range-fill';
                case '3C':
                    return 'ri-smartphone-fill';

            }

        },
        uppage() {
            this.pagenumber--
        },
        nextpage() {
            this.pagenumber++
        },
        clicktabitem(val, name) {
            this.currentstadium = name;
            this.tabcurrentindex = val;
        },
        activitycata(val) {
            this.currentcata = val

            console.log('有點到', val);
        },
        keywordsearch(val) {
            this.keyword = val;
            console.log(val);
        },
        deleact(eventid) {

            this.sclist.forEach((item, index) => {
                if (item[17] == eventid) {
                    this.deleactindex = index
                }
            });

            this.isdele = true;

        },
        deleacthandler(type) {
            console.log(type)
            if (type == "取消") {
                this.isdele = false
            } else if (type == "刪除") {
                this.isdele = false
                axios({
                        method: 'get',
                        url: 'https://script.google.com/macros/s/AKfycbzc8eMlt2eibZe9RqhJe3m3rjBr_T2wPZPyoGc0LAhVOZBgT75DmYkb1DinDd0iosrbzA/exec',
                        params: {
                            eventid: this.sclist[this.deleactindex][17],
                        }
                    })
                    .then((response) => {
                        console.log(response.data)

                    })
                    .catch((error) => {
                        console.error(error)
                    })
                location.reload();

            }

        },
        timefilter(val) {
            var today = new Date();
            this.timestatus = val;
            if (val == "closetofar") {
                let newdate = (today.getMonth() + 1) + '/' + today.getDate()
                this.filterlist = this.filterlist.filter((item) => {
                    return this.timecompare(item, newdate, 30)
                })

            } else if (val == "fartoclose") {
                let newdate = (today.getMonth() + 1) + '/' + today.getDate()
                this.filterlist = this.filterlist.filter((item) => {
                    console.log(item)
                    return this.timecompare(item, newdate, 70)
                })
            }
        },
        timecompare(item, currentdatetime, day) {
            let compareitem = item[1].split('/')
            let splitlist = currentdatetime.split('/');
            let num
            let num2
            if (compareitem[1] == "02") {
                num = parseInt(compareitem[1]) * 28 + parseInt(compareitem[2])
            }
            if (compareitem[1] == "04" || compareitem[1] == "06" || compareitem[1] == "09" || compareitem[1] == "11") {
                num = parseInt(compareitem[1]) * 30 + parseInt(compareitem[2])
            }
            if (compareitem[1] == "01" || compareitem[1] == "03" || compareitem[1] == "05" || compareitem[1] == "07" || compareitem[1] == "08" || compareitem[1] == "10" || compareitem[1] == "12") {
                num = parseInt(compareitem[1]) * 31 + parseInt(compareitem[2])
            }

            if (splitlist[0] == "4" || splitlist[0] == "6" || splitlist[0] == "9" || splitlist[0] == "11") {
                num2 = parseInt(splitlist[0]) * 30 + parseInt(splitlist[1])
            }
            if (splitlist[0] == "1" || splitlist[0] == "3" || splitlist[0] == "5" || splitlist[0] == "7" || splitlist[0] == "8" || splitlist[0] == "10" || splitlist[0] == "112") {
                num2 = parseInt(splitlist[0]) * 31 + parseInt(splitlist[1])
            }
            let minusnum = num2 - num;
            if (minusnum < day) {
                console.log('有歐', minusnum)
                return item[1]
            }
        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
        toactmember(eventid) {
            this.$store.state.currentlistinfo = this.sclist[eventid]
            window.sessionStorage.setItem('storeeventinfo', JSON.stringify(this.sclist[eventid]))
            this.$router.push({
                name: 'ActivityMemberManage',
                params: {
                    eventid: this.sclist[eventid][17]
                }
            })
        }
    },
    components: {
        HeaderBar,
        Sidemenu,
        SearchBar,
        // Tabitem,
        Dropdown,
        // Timefilter,
        Activitylist,
        Deleactpopup
    }
}
</script>

