<template>
<div class="checkinfo">
    <div class="checkinfo-actblock">
        <Actblock :actname="actinfo.actname"  :isfree="actinfo.fee" :acttype="actinfo.type" :describe="actinfo.describe" :taglist="actinfo.tag" :time="actinfo.time" :date="actinfo.date" :pos="actinfo.loc" :quota="actinfo.quota" :actwaitingnum="actinfo.available" :actpic="actinfo.actpic" :price="actinfo.price"  bgz='cover'></Actblock>
    </div>
    <div class="checkinfo-btnwrap">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="上一步" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn v-show="editmode==false" btnwidth="193" btnheight="57" btnfz="24" btnname="新增活動" @clickhandler="addevent"></fillbtn>
        <fillbtn v-show="editmode==true" btnwidth="193" btnheight="57" btnfz="24" btnname="確認變更" @clickhandler="saveedit"></fillbtn>
    </div>

</div>
</template>

<script>
import Actblock from '../components/Actblock'
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {

        }
    },
    computed: {
        actinfo() {
            return this.$store.state.actinfo;
        },
        editmode() {
            return this.$store.state.editmode;
        },
          hostid(){
            return this.$store.state.userinfo[3]
        }
    },
    methods: {
        upstep() {
            this.$store.commit('minuscreateactindex')
        },
        addevent() {
            this.$store.commit('showliffurlblock')
            this.$store.dispatch('postactinfo')
        },
        saveedit() {
            this.$store.dispatch('editactinfo')
            this.$router.push({
                name: 'ActivityPage',
                params: {
                    hostid: this.hostid
                }
            })
        }
    },
    components: {
        Actblock,
        fillbtn
    }
}
</script>

