<template>
<div class="inputnooutline">
    <div class="inputname"><span>{{headername}}</span></div>
    <input v-show="type!='password'" :class="{invalid:invalid,none:inputdefault}" ref="Inputname" type="text" :placeholder="placeholder" @input="emittext" v-model="inputtext" :style="{width:width+'px'}" required>
    <input v-show="type=='password'" type="password" :placeholder="placeholder" @input="emittext" v-model="inputtext" :style="{width:width+'px'}">
</div>
</template>

<script>
export default {
    props: ['headername', 'width', 'placeholder', 'type', 'alreadydata'],
    data() {
        return {
            inputtext: '',
            inputheader: this.headername,
            invalid: false,
            inputdefault: true
        }
    },
    computed: {

    },
    mounted() {
        this.inputtext = this.alreadydata
    },
    methods: {
        emittext() {
            let empty = true
            if (this.inputtext == '') {
                empty = true
                this.invalid = true
                this.inputdefault = false
            } else {
                empty = false
                this.invalid = false
                this.inputdefault = true
            }
            this.$emit('inputtexthandler', this.inputtext, this.inputheader, empty)
        }
    }
}
</script>

