<template>
<button class="outlinebtn" @click="delehandler(btnname)" :style="hovercolor">
    {{btnname}}
</button>
</template>

<script>
export default {
    props: ['btnname', 'textcol', 'bordercol', 'hovercol','hovertextcol'],
    data() {
        return {

        }
    },
    methods: {
        delehandler(type) {
            this.$emit('emitdele', type)

        }
    },
    computed: {
        hovercolor() {
            return {
                '--border-color': this.bordercol,
                '--bgcolor-hover': this.hovercol,
                '--textcolor-hover':this.hovertextcol,
                '--text-color':this.textcol
            }
        }
    }
}
</script>

<style lang="scss">
// $hovercolor: #CDF2DF;

.outlinebtn {
    cursor: pointer;
    width: 112px;
    height: 36px;
    padding: 10px 15px;
    border-radius: 4px;
    border: solid 1px var(--border-color);
    background-color: white;
  
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: 1.25px;
    color: var(--text-color);
    transition: 0.3s;

}

.outlinebtn:hover {
    background: var(--bgcolor-hover);
    color: var(--textcolor-hover);
}
</style>
